<template>
  <div>
      <!-- 面包屑导航区域 -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home'}">首页</el-breadcrumb-item>
          <el-breadcrumb-item>权限管理</el-breadcrumb-item>
          <el-breadcrumb-item>权限列表</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 卡片视图 -->
      <el-card>
          <!-- 添加角色按钮区 -->
          <el-row>
              <el-col>
                  <el-button type="primary"
                  @click="addDialogVisible = true"
                  >添加角色</el-button>
              </el-col>
          </el-row>
          <!-- 角色列表 -->
          <el-table :data="roleList" border stripe>
              <!-- 展开列 -->
              <el-table-column type="expand">
                  <template slot-scope="scope">
                      <el-row
                      :class="['bdbottom',index1 === 0 ? 'bdtop': '','vcenter']"
                      v-for="(item1, index1) in scope.row.children"
                      :key="item1.id">

                          <!-- 渲染一级权限 -->
                          <el-col  :span="5">
                              <el-tag
                              closable
                              @close="removeRightById(scope.row, item1.id)">{{ item1.authName}}
                              <i class="el-icon-caret-right"></i>
                              </el-tag>
                          </el-col>
                          <!-- 渲染二级、三级权限 -->
                          <el-col :span="19">
                              <el-row
                              :class="[index2 === 0 ? '':'bdtop','vcenter']"
                              v-for="(item2, index2) in item.children"
                              :key="item2.id">

                              <!-- 渲染二级权限 -->
                              <el-col :span="6">
                                <el-tag
                                type="success"
                                closable
                                @close="removeRightById(scope.row, item2.id)">{{ item2.authName }}</el-tag>
                                <i class="el-icon-caret-right"></i>
                                </el-col>
                                <!-- 渲染三级权限 -->
                                <el-col :span="18">
                                <el-tag type="warning"
                                closable
                                @close="removeRightById(scope.row, item3.id)"
                                v-for="item3 in item2.children"
                                :key="item3.id">{{ item3.authName }}</el-tag>
                                </el-col>
                              </el-row>
                          </el-col>
                      </el-row>
                  </template>
              </el-table-column>
              <!-- 索引列 -->
              <el-table-column type="index"></el-table-column>
              <el-table-column label="角色名称" prop="roleName"></el-table-column>
              <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
              <el-table-column label="操作" width="300px">
               <template slot-scope="scope">
                   <el-button
                   size="mini"
                   type="primary"
                   icon="el-icon-edit"
                   @click="showEditDialog(scope.row.id)">
                   编辑</el-button>
                   <el-button
                   size="mini"
                   type="danger"
                   icon="el-icon-delete"
                   @click="removeRoleById(scope.row.id)">删除</el-button>
                   <el-button
                   @click="showSetRightDialog(scope.row)"
                   size="mini"
                   type="warning"
                   icon="el-icon-setting">分配权限</el-button>
               </template>
               </el-table-column>
          </el-table>
      </el-card>
      <!--  -->
      <!-- 添加角色的对话框 -->
    <el-dialog
      title="添加角色"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!-- 内容主体区域 -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="80px"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改用户的对话框 -->
    <el-dialog title="修改角色" :visible.sync="editDialogVisible" width="50%">
      <!-- 内容主体区域 -->
      <el-form
        :model="editForm"
        :rules="addFormRules"
        ref="editFormRef"
        label-width="80px"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
    </el-dialog>
               <!-- 分配权限的对话框 -->
                <el-dialog
                    title="分配权限"
                    :visible.sync="setRightDialogVisible"
                    width="50%"
                    @close="setRightDialogClosed"
                    >
                    <!-- 内容主体区域 -->
                    <el-tree
                    ref="treeRef"
                    :data="rightslist"
                    :props="treeProps"
                    show-checkbox
                    node-key="id"
                    default-expand-all
                    :default-checked-keys="defKeys"
                    ></el-tree>
                    <!-- 底部区域 -->
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="setRightDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="allotRights">确 定</el-button>
                    </span>
                </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 所有角色列表数据
      roleList: [],
      // 控制分配权限对话框显示与隐藏
      setRightDialogVisible: false,
      // 所有权限数据
      rightslist: [],
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      defKeys: [],
      roleId: '',
      addDialogVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' }
        ]
      },
      // 控制修改用户对话框的显示与隐藏
      editDialogVisible: false,
      // 查询到的角色的信息对象
      editForm: {}
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    // 获取所有角色的列表
    async getRolesList () {
      const { data: result } = await this.$http.get('roles')
      if (result.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      this.roleList = result.data
    },
    async removeRightById (role, rightId) {
      // 弹窗提示是否删除数据
      const confirmResult = await this.$confirm(
        '此操作将永久删除该权限，是否继续？',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(error => error)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已经取消')
      }
      const { data: result } = await this.$http.delete(
        'roles/' + role.id + '/rights/' + rightId
      )
      if (result.meta.status !== 200) {
        return this.$message.error('删除权限失败')
      }
      this.$message.success('删除权限成功')
      role.children = result.data
    },
    // 展示分配权限的对话框
    async showSetRightDialog (role) {
      this.roleId = role.id
      // 获取所有权限数据
      const { data: result } = await this.$http.get('rights/tree')
      if (result.meta.status !== 200) return this.$message.error('获取权限数据失败')
      // 获取到的权限数据保存到rightslist中
      this.rightslist = result.data
      // 获取三级节点的 id
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    getLeafKeys (node, arr) {
      // 如果当前节点不包含children属性，则是三级权限节点
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    setRightDialogClosed () {
      this.defKeys = []
    },
    /* showSetRightDialog (role) {
      this.roleId = role.id
    }, */
    async allotRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: result } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (result.meta.status !== 200) return this.$message.error('分配权限失败')
      this.$message.success('分配权限成功')
      this.getRolesList()
      this.setRightDialogVisible = false
    },
    // 监听添加用户对话框的关闭事件
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 添加角色操作
    addRole () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: result } = await this.$http.post('roles', this.addForm)
        if (result.meta.status !== 201) {
          return this.$message.error('添加角色失败！')
        }
        this.$message.success('添加角色成功！')
        this.addDialogVisible = false
        this.getRolesList()
      })
    },
    // 展示编辑用户的对话框
    async showEditDialog (id) {
      this.editDialogVisible = true
      const { data: result } = await this.$http.get('roles/' + id)
      if (result.meta.status !== 200) {
        return this.$message('查询用户信息失败！')
      }
      this.editForm = result.data
    },
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        // console.log(valid)
        if (!valid) return
        // 发起修改用户信息的数据请求
        const { data: result } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          { roleName: this.editForm.roleName, roleDesc: this.editForm.roleDesc }
        )
        if (result.meta.status !== 200) {
          return this.$message.error('更新角色失败')
        }
        // 关闭对话框
        this.editDialogVisible = false
        // 重新获取用户列表
        this.getRolesList()
        // 提示修改成功
        this.$message.success('更新角色信息成功')
      })
    },
    // 根据id删除对应的用户信息
    async removeRoleById (id) {
      // 弹框提示用户是否删除数据
      const confirmResult = await this.$confirm(
        '此操作将永久删除该角色，是否继续？',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(error => error)
      // console.log(confirmResult) // 确定：'confirm' 取消：报错:cancel catch之后就是'canel'
      if (confirmResult !== 'confirm') {
        return this.$message.info('已经取消删除')
      }
      const { data: result } = await this.$http.delete('roles/' + id)
      if (result.meta.status !== 200) {
        return this.$message.error('删除角色失败')
      }
      this.$message.success('删除角色成功')
      this.getRolesList()
    }

  }
}
</script>

<style>
.el-tag {
    margin: 7px;
}
.bdtop {
    border-top: 1px solid #eee;
}
.bdbottom {
    border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}

</style>
